.character-list {
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	text-align: center;
	overflow-y: scroll;

	&__main {
		width: 100%;
		display: flex;
		flex-direction: column;
		text-align: center;
		justify-content: center;
	}

	&__form {
		display: grid;
		grid-template-rows: auto auto;
		padding-bottom: 15px;
		width: 300px;
		align-self: center;

		&--label {
			font-size: 25px;
			text-shadow: 1px 1px #47b5cd;
		}
		&--container {
			margin: 10px 0;
			height: 32px;
			background-color: whitesmoke;
			border: 2px solid #122988;
			border-radius: 10px;
			display: flex;
			align-items: center;
			&:hover {
				border: 2px solid #47b5cd;
				cursor: auto;
			}
		}
		&--icon {
			margin: 0 8px 0 6px;
			color: #47b5cd;
			font-size: 16px;
		}
		&--input {
			width: 250px;
			background-color: whitesmoke;
			border: none;
			font-size: 15px;
			&:hover {
				font-style: italic;
			}
		}
	}
	&__list {
		padding: 0;
		list-style: none;
		display: grid;
		grid-template-columns: repeat(auto-fill, 200px);
		grid-gap: 20px;
		justify-content: center;
		&--link {
			color: #efefef;
			font-size: 15px;
			font-weight: bold;
			text-decoration: none;
			text-shadow: 1px 2px #122988;
		}
	}
}
